import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { EduhackProvider } from '../../providers/eduhack/eduhack';

/**
 * Generated class for the ActivitiesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-activities',
  templateUrl: 'activities.html',
})
export class ActivitiesPage {
activities : any;
id :any;
  constructor(public navCtrl: NavController, public navParams: NavParams, private eduprov : EduhackProvider) {
    this.id = this.navParams.data;
    console .log(this.id);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ActivitiesPage');
    this.getActivities();
    
  }


  getActivities(){
    this.eduprov.getActivities(this.id).subscribe(  data => {
      this.activities = data;
      console.log(data);
      console.log(this.activities);
      
      
    },

    err => {
      console.log(err);
    },
    () => {
      console.log('done');
      
      
   }
  );

  }
}



