import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { EduhackProvider } from '../../providers/eduhack/eduhack';
import { SubjectsPage } from '../subjects/subjects';

/**
 * Generated class for the CategoryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-category',
  templateUrl: 'category.html',
})
export class CategoryPage {

categories : any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private eduprov : EduhackProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CategoryPage');
    this.getCategories();
  }


  getCategories(){

    this.eduprov.getCategories().subscribe(  data => {
      this.categories = data;
      console.log(data);
      console.log(this.categories[0]);
      
      
    },

    err => {
      console.log(err);
    },
    () => {
      console.log('done');
      
      
   }
  );
  }


  openSubjects(id :number ){

    console.log(this.categories[id].Id);
    this.navCtrl.push(SubjectsPage,this.categories[id].Id)
  }
}



