import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { EduhackProvider } from '../../providers/eduhack/eduhack';
import { ActivitiesPage } from '../activities/activities';

/**
 * Generated class for the SubjectsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-subjects',
  templateUrl: 'subjects.html',
})
export class SubjectsPage {

  id : number;
  subjects :any;
  constructor(public navCtrl: NavController, public navParams: NavParams, private eduprov : EduhackProvider) {

    this.id = this.navParams.data
    console.log(this.id);

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SubjectsPage');
    this.getSubject();
  }


  getSubject(){
      var obj :any;
    this.eduprov.getSubject(this.id).subscribe(  data => {
      this.subjects = data;
      console.log(this.subjects);
      //this.subjects = Object.keys(obj).map(key => ( this.subjects[key]));// object to array
     
      
      
      
    },

    err => {
      console.log(err);
    },
    () => {
      console.log('done');
      
      
   }
  );
  }

  getActivities(id : number){
    console.log(this.subjects.CategoryId);
    this.navCtrl.push(ActivitiesPage, this.subjects[id].CategoryId);
  }
}



