import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { EduhackProvider } from '../../providers/eduhack/eduhack';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  scores : any;
  constructor(public navCtrl: NavController, private eduprov : EduhackProvider) {

  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad CategoryPage');
    this.getCategories();
  }
  getCategories(){

    this.eduprov.getPlayedGames().subscribe(  data => {
      this.scores = data;
      console.log(data);
      
      
      
    },

    err => {
      console.log(err);
    },
    () => {
      console.log('done');
      
      
   }
  );
  }
}



